.side-drawer{
    height: 100%;
    background: white;
    box-shadow: 2px 0px 5px  rgba(0, 0,0, 0.5);
    position:fixed;
    top: 0;
    left: 0;
    width: 90%;
    max-width: 900px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
  
  }
  .side-drawer ul{
      height: 100%;
      list-style:none;
      display: flex;
      flex-direction: column;
      justify-content:center ;
      
  }
  .side-drawer.open{
      transform: translateX(0);
  }
  .side-drawer li{
      margin: 0.5rem 0;
  }
  .side-drawer a{
      color: blueviolet;
      text-decoration-style: none;
      font-size: 1.2rem;
  }
  .side-drawer a:hover,
  .side-drawer a:active{
      color: aqua;
  }

  @media(min-width: 969px){
      .side-drawer{
          display: none;
      }
  }